}
#cpanel #group-button-cpanel .fa:hover {
	color: #596B8F;
	-webkit-transform: scale(1.3);
	    -ms-transform: scale(1.3);
	        transform: scale(1.3);
}
#cpanel .list-group i {
	margin-right: 5px;
