	-o-transition: all 0.5s linear;
	transition: all 0.5s linear;
}
#cpanel .list-group i:hover {
	-webkit-transform: scale(1.3);
	    -ms-transform: scale(1.3);
	        transform: scale(1.3);
}
#edit i:hover {
