#products .hr-group {
  float: left;
}
#products .hr-group .hr-content {
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
  margin: 0 !important;
}
#products .btn-group-nav i.fa-chevron-left, #products .btn-group-nav i.fa-chevron-right {
  padding: 10px;
  background: #ff6644;
  color: white;
}
#products .btn-group-nav i.fa-chevron-left:hover, #products .btn-group-nav i.fa-chevron-right:hover {
  color: #23527c ;
}
#products .btn-group-nav i.fa-chevron-left {
  margin-right: 5px;
}
.content-slider-product {
  min-height: 436px !important;
}
.slick-dots {
	bottom: initial;
}
@media (max-width: 479px) {
	p#about {
		font-size: 25px;
	}
}
@media (min-width: 768px) {
	.about-us-description {
		overflow: auto;
	}
}
@media (max-width: 991px) {
	#thumb-product {
		height: 286px !important;
	}
}


