a:link {
    text-decoration: none;
}
#hire-detail {
    background: #eee;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}
.hd-header-thumbnail {
    background-size: cover !important;
    background-position: center;
    margin: 0 15px;
}
#hire-detail header .navbar-default{
    top: 0;
}
#hire-detail .hire-detail-header {
    background: white;
}
#hire-detail .hire-detail-header h1 {
    margin-top: 10px;
}
#hire-detail .hire-detail-content {
    padding: 30px 15px;
    background: white;
    margin-bottom: 30px;
}
.hire-detail-content-left  {
    margin-bottom: 30px;
}
.hd-detail-info {
    padding: 10px;
    border: 1px solid rgba(0,185,242,.5);
    background: #f7fdff;
}
.hd-detail-info-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.hd-detail-info-item i {
    margin: 10px;
}
.hd-detail-info-main {
    font-size: 16px;
}
#timeLeft {
    color: red;
}

.hd-detail-diff-group {
    margin-top: 30px;
    background: #f7fdff;
}
.hd-detail-diff-top {
    padding: 10px !important;
    border: 1px solid rgba(0,185,242,.5);
    border-bottom: 0;
}
.hd-detail-diff {
    padding: 10px;
    border: 1px solid rgba(0,185,242,.5);
}
.hd-detail-diff-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px 0;
}
.hd-detail-diff-thumbnail {
    margin-right: 10px;
    background-position: center;
    background-size: cover !important;
}
#hire-detail .send-cv {
    right: 30px;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}
#hire-detail .send-cv-small {
    margin: auto;
    margin-bottom: 20px;
}
@media (min-width: 600px) {
    .send-cv-small {
        display: none;
    }
}
@media (max-width: 599px) {
    .send-cv {
        display: none;
    }
}
@media (max-width: 991px) {
    .hire-detail-header h2 {
        font-size: 15px;
    }
}
@media (min-width: 1200px) {
    #hire-detail .container {
        width: 992px;
    }
}


