.bl-post {
    margin-top: 50px;
    margin-bottom: 50px;
}
.blog-page .thumbnail {
    background-size: cover !important;
}
.blog-date {
    padding: 0;
    color: #76889A;
    display: block;
    text-align: center;
    background: #f5f7f9;
    border-radius: 5px;
    float: left;
}
.blog-date #date {
    display: block;
    font-size: 32px;
    font-weight: 400;
    border-bottom: 1px solid rgba(178, 196, 214, 0.3);
    padding: 5px 0;
}
.blog-date #month {
    font-size: 25px;
}
.blog-content {
    border-radius: 3px;
    padding: 15px;
    background: #f5f7f9;
}
.blog-content img {
    width: 100%;
    margin: auto;
    display: block;
}
ul#bl-list-post {
    padding: 0;
    list-style: none;
}
@media (max-width: 767px) {
  .blog-date {
    display: none;
  }
}


