/* ---Start Works.css */
.modal-dialog-index {
    position: relative;
    top: 50% !important;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) !important;
        -ms-transform: translate(-50%, -50%) !important;
            transform: translate(-50%, -50%) !important;
    height: 50vh;
}
.modal-dialog-index .modal-body {
	overflow: auto;
}
.modal-dialog-index .modal-body .thumbnail-modal {
	height: 100%;
	border-radius: 3px;
	background-size: cover !important;
}
.modal-dialog-index .modal-body .slider-modal-index {
	height: 60%;
	position: absolute;
	bottom: 0;
	width: 100%;
	right: 0;
}
.modal-dialog-index .modal-body .slider-modal-index .item-silder-modal-index {
	background-size: cover !important;
}
.modal-dialog-index .modal-body .slider-modal .item-modal-index {
	height: 100%;
}
#des-product {
    display: -ms-flexbox;
    display: flex;
    margin-right: 5px;
}
.carousel-inner .item {
	height: 100%;
	width: 100%;
	background-size: cover !important;
	background-repeat: no-repeat !important;
}
.slick-slide {
	height: 60%;
}
.slick-slide .fa:hover {
	background: black;
}
.slick-slide .fa.fa-arrow-right, .fa.fa-arrow-left {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    background: white;	
	padding: 3px;
	border-radius: 50%;
	opacity: 0.7;
	cursor: pointer;
}
.fa.fa-arrow-right {
	right: 20px;
}
.fa.fa-arrow-left {
	left: 20px;
}
li a.active {
	background: #ff6644 !important;
	color: white !important;
}
.example-leave {
   opacity: 1;
}
.example-leave-active {
   opacity: 0.01;
   -webkit-transition: opacity 300ms ease-in;
   -o-transition: opacity 300ms ease-in;
   transition: opacity 300ms ease-in;
}
#our-works .carousel-control {
	opacity: 0;
	-webkit-transition: opacity 1s linear;
	-o-transition: opacity 1s linear;
	transition: opacity 1s linear;
}
#our-works .carousel-control:hover {
	opacity: 1;
}
#our-works .carousel-inner > .item > img {
    height: inherit;
}
.modal-body {
    height: 100%;
}
@media (max-width: 479px) {
  .modal-dialog-index {
    top: 300px !important;
  }
}
@media (min-width: 768px) {
	#our-works .modal-body {
		padding: 60px 15px;
	}
}
@media (min-width: 992px) {
    .info-date {
    	display: -ms-flexbox;
    	display: flex;
    }
    p#date-created {
        width: 50%;
    }
}
@media (min-width: 1200px) {
	.modal-dialog-index {
		height: 70vh;
	}
}/* --- End Works.css --- */

/* --- Start Skills.css --- */
#ok-logo-customer {
	background-size: cover !important;
  background-position: center !important;
  min-width: 100px;
}
.hr-doc {
  height: 100px;
  background: white;
  margin: 0 10px;
  width: 5px;
}
#feedback-customer {
  display: block;
  padding: 0 10px;
}
@media (max-width: 767px) {
  #our-stats {
    height: initial !important;
  }
  main #stats .footer-our-skills .content {
    position: relative;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}
.slick-arrow {
    height: auto;
    width: auto;
    z-index: 1;
}
.slick-next {
    right: 0;
}
.slick-prev {
	left: 0;
}
.slick-prev:before, .slick-next:before {
    font-family: "Glyphicons Halflings", "slick", sans-serif;
	font-size: 40px;
	opacity: 0;
	-webkit-transition: opacity .3s linear;
	-o-transition: opacity .3s linear;
	transition: opacity .3s linear;
}
.slick-prev:before:hover, .slick-next:before:hover {
	opacity: 1;
}
.slick-prev:before { 
	margin-left: 5px;
	content: "\e257"; 
	color: white;
}
.slick-next:before { 
	margin-right: 5px;
	content: "\e258"; 
	color: white;
}/* --- End Skills.css --- */

